<template lang="html">
  <div>
    <div id="skills" class="my-skills text-center">
    	<div class="container">
    		<h3>个人技能</h3>
    		<div class="strip text-center"><img src="../assets/images/skill.png" alt=" "/></div>
    		<div class="skill-grids">
    					<div class="col-md-2 skills-grid text-center">
    						<div class="circle" id="circles-1"></div>
    						<p>HTML(5) / CSS(3)</p>
    					</div>
    					<div class="col-md-2 skills-grid text-center">
    						<div class="circle" id="circles-2"></div>
                <p>JAVASCRIPT</p>
    					</div>
    					<div class="col-md-2 skills-grid text-center">
    						<div class="circle" id="circles-3"></div>
                <p>REQUIRE</p>
    					</div>
    					<div class="col-md-2 skills-grid text-center">
    						<div class="circle" id="circles-4"></div>
                <p>ES6</p>
    					</div>
    					<div class="col-md-2 skills-grid text-center">
    						<div class="circle" id="circles-5"></div>
    						<p>VUE</p>
    					</div>
    					<div class="col-md-2 skills-grid text-center">
    						<div class="circle" id="circles-6"></div>
    						<p>PHOTOSHOP</p>
    					</div>

    					<div class="clearfix"> </div>
    		</div>
    	</div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {

    }
  },
  mounted() {
    this.show();
  },
  methods: {
    show: function() {
      let colors = [
        ['#6ed4c0', '#ffffff'],
        ['#6ed4c0', '#ffffff'],
        ['#6ed4c0', '#ffffff'],
        ['#6ed4c0', '#ffffff'],
        ['#6ed4c0', '#ffffff'],
        ['#6ed4c0', '#ffffff']
      ];
      let datas = [0];
      for (var i = 1; i < 7; i++) {
        var child = document.getElementById('circles-' + i),
          percentage = 30 + (i * 10);
          datas.push(this.random(70,95))
        Circles.create({
          id: child.id,
          percentage: datas[i],
          radius: 80,
          width: 10,
          number: datas[i] / 1,
          text: '%',
          colors: colors[i - 1]
        });
      }
    },
    random: function(m, n) {
      return parseInt(Math.random() * (m - n) + n);
    }
  }

}
</script>

<style lang="css" scoped>
</style>



