<template lang="html">
  <div>
    <div class="client-back"></div>
    <div class="blog" id="blog">
    	<div class="container">
    		<div class="blog-info text-center">
    			<h3>案例</h3>
    			<div class="strip text-center"><img src="../assets/images/blog.png" alt=" "/></div>
    		</div>
    		<div class="blog-grids">
    			<div class="col-md-4 col-xs-12 work-grids" >
    				<div class="blog-grid">
    					<a href="http://www.suxiumall.com/shop/"><img src="../assets/images/c1.png" alt=""/></a>
    					<div class="blog-text">
    						<a href="http://www.suxiumall.com/shop/">苏绣民品网</a>
    						<div class="stripa"></div>
    						<p>苏绣名品网基于产业经济发展要求的独立服务，包含网络营销推广、电子商务服务，以及基于线下刺绣产业资源的业务支撑和运营管理体系，将实现对苏绣精品、名品的集中销售和集中推广，为苏绣营销模式改革寻求新的突破口。</p>
    					</div>

    				</div>
    			</div>
    			<div class="col-md-4 col-xs-12 work-grids">
    				<div class="blog-grid">
    					<a :href="[url]"><img src="../assets/images/c2.png" alt=""/></a>
    					<div class="blog-text">
    						<a :href="{url}">世泊汇</a>
    						<div class="stripa"></div>
    						<p>中华网旗下跨境电商平台,将结合自身优势推出“反向跨境”业务，将中国的优质商品出口到世界，让更多的人体验中国制造的优秀产品，在更高的层面实现世舶汇品牌在电子商务领域的价值。</p>
    					</div>

    				</div>
    			</div>
    			<div class="col-md-4 col-xs-12 work-grids">
    				<div class="blog-grid">
    					<a href="http://www.haijiu365.com/"><img src="../assets/images/c3.png" alt=""/></a>
    					<div class="blog-text">
    						<a href="http://www.haijiu365.com/">海酒网</a>
    						<div class="stripa"></div>
    						<p>海酒网是一个B2B葡萄酒类交易平台，致力于为批发采购商、投资机构及和世界各地葡萄酒生产商搭建可靠的供需平台，并提供一站式商务解决方案。隶属于深圳前海玖加电子商务有限公司。</p>
    					</div>

    				</div>
    			</div>

    			<div class="clearfix"></div>
    		</div>
    	</div>
    </div>
  </div>
</template>

<script>
export default {
  data(){
    return {
      url:'http://mall.china.com/'
    }
  },
  mounted(){
    var windowWidh = $('html , body').width();
    if (windowWidh <= 640) {
      this.url='http://mall.china.com/wap'
    }
  }
}
</script>

<style lang="css" scoped>
</style>



