<template lang="html">
	<div>
		<div class="banner" id="home" >
			<div class="container">
				<div class="top-header row">
					<div class="logo">
						<a href="index.html"><img src="../assets/images/logo.png" alt="" /></a>
					</div>
					<div class="top-nav">
						<div class="nav-icon">
							<a href="#" class="right_bt" id="activator" @click='navShow'><span> </span> </a>
						</div>
						<div class="box" id="box" ref='box'>
							<div class="box_content">
								<div class="box_content_center">
									<div class="form_content">
										<div class="menu_box_list">
											<ul id="navBox" ref='navBox'>
                        <li v-for="(val,index) in navList">
                          <a :href="['#'+toId[index]]" @click='toScroll(toId[index],index)'><span>{{val}}</span></a>
                        </li>
												<div class="clearfix"> </div>
											</ul>
										</div>
										<a class="boxclose" id="boxclose" @click='navHide'> <span> </span></a>
									</div>
								</div>
							</div>
						</div>
					</div>
					<div class="clearfix"> </div>
				</div>
				<div class="banner-info">
					<div class="banner-left">
						<img class="img-rounded" src="../assets/images/1.png" alt="" />
					</div>
					<div class="banner-right">
						<h1>易囿权</h1>
						<div class="border"></div>
						<h2>web前端开发</h2>
						<!-- <a href="#">DOWNLOAD MY RESUME</a> -->
					</div>
					<div class="clearfix"></div>
				</div>
			</div>
		</div>
	</div>
</template>
<!-- 886 246 659 097 842 725 -->
<script>
export default {
  data(){
    return {
      navList:['首页','关于','技能','经历','案例','联系'],
			toId:['home','about','skills','education','blog','contact']
    }
  },
  methods: {
    navShow: function() {
      var dom = this.$refs.box;
      $(dom).animate({
        top: 0
      })
    },
    navHide: function() {
      var dom = this.$refs.box;
      $(dom).animate({
        top: '-400px'
      })
    },
    toScroll:function(val,index){
      var top = document.getElementById(val);
			var dom = this.$refs.box;
      $('html , body').animate({
        scrollTop : $(top).offset().top
      });
			$(dom).animate({
        top: '-400px'
      })
    }

  },
  created() {

  }
}
</script>

<style>

</style>



